import React from 'react';
import { Button } from '../ui/button';

interface AuthButtonsProps {
  isLoggedIn: boolean;
  onLogin: () => void;
  onLogout: () => void;
}

export function AuthButtons({ isLoggedIn, onLogin, onLogout }: AuthButtonsProps) {
  if (isLoggedIn) {
    return (
      <Button 
        variant="outline" 
        onClick={onLogout}
        className="ml-2"
      >
        Logout
      </Button>
    );
  }

  return (
    <Button 
      onClick={onLogin}
      className="ml-2"
    >
      Login
    </Button>
  );
} 